/* AddNewFormPopup.css */

.computer-form {
    display: flex;
    flex-direction: column;
    align-items: center;
    z-index: 1000;
    font-size: 20px;

}

.form-label {
    display: flex;
    flex-direction: column;
    margin-bottom: 10px;

}

.form-input {
    width: 100%;
    padding: 8px;
    margin-top: 5px;
}

.form-buttons {
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
}

.close-btn,
.add-button {
    padding: 10px;
    cursor: pointer;
}

.popup {
    position: fixed;
    top: 0;
    left: 0;

    width: 100%;
    height: 100%;
    max-height: 800px;
    background-color: rgba(0, 0, 0, 0.2);
    display: grid;
    justify-content: center;
}

.popup-inner {
    /*position: relative;*/
    padding: 20px;
    width: 100%;
    max-width: 500px;
    background-color: #FFF;


}
.computer-form {
    display: grid;
    grid-template-columns: 1fr 1fr; /* Dwie kolumny o równej szerokości */
    gap: 60px; /* Dodaj margines między kolumnami */
}

.form-column-left {
    grid-column: 1 / 2;
}

.form-column-right {
    grid-column: 2 / 2;
}

.form-buttons {
    grid-column: span 2; /* Rozciągnij na obie kolumny */
    display: flex;
    justify-content: space-between;
    /*margin-top: 5px;*/
}

.popup-select {
    /*width: 20%;*/

    display: flex;
    align-items: center;
    justify-content: center;
    background-color: white;
    padding: 5px;
    /*width: 100%;*/
}

.popup-select select {
    margin-left: 10px; /* Dodatkowy odstęp od lewej strony selecta */
}